	module.exports = {"data":{"postsJson":{"title":"Signs Two & Three","questionsTitle":"LEARNING FROM THE PAST","display":"block","bookName":"John","ref":"4:43-5:47","html":"<p>   <strong>4</strong><u>43</u>After the two days he went out from there and went into Galilee. <u>44</u>For Jesus himself testified that a prophet has no honor in his own country. <u>45</u>So when he came into Galilee, the Galileans received him, having seen all the things that he did in Jerusalem at the feast, for they also went to the feast. <u>46</u>Jesus came therefore again to Cana of Galilee, where he made the water into wine. There was a certain nobleman whose son was sick at Capernaum. <u>47</u>When he heard that Jesus had come out of Judea into Galilee, he went to him, and begged him that he would come down and heal his son, for he was at the point of death. <u>48</u>Jesus therefore said to him,</p><h1>JESUS</h1><h2>Unless you see signs and wonders, you will in no way believe.</h2><p>   <u>49</u>The nobleman said to him,</p><h1>AN OFFICIAL OF CAPERNAUM</h1><h4>Sir, come down before my child dies.</h4><p><u>50</u>Jesus said to him,</p><h1>JESUS</h1><h2>Go your way. Your son lives.</h2><p>The man believed the word that Jesus spoke to him, and he went his way. <u>51</u>As he was now going down, his servants met him and reported, saying</p><h1>AN OFFICIAL’S SERVANTS</h1><h4>Your child lives!</h4><p><u>52</u>So he inquired of them the hour when he began to get better. They said therefore to him,</p><h1>AN OFFICIAL’S SERVANTS</h1><h4>Yesterday at the seventh hour,* the fever left him.</h4><p><u>53</u>So the father knew that it was at that hour in which Jesus said to him, “Your son lives.” He believed, as did his whole house. <u>54</u>This is again the second sign that Jesus did, having come out of Judea into Galilee. <br />   <strong>5</strong><u>1</u>After these things, there was a feast of the Jews, and Jesus went up to Jerusalem. <u>2</u>Now in Jerusalem by the sheep gate, there is a pool, which is called in Hebrew, “Bethesda”, having five porches. <u>3</u>In these lay a great multitude of those who were sick, blind, lame, or paralyzed, waiting for the moving of the water; <u>4</u>for an angel went down at certain times into the pool, and stirred up the water. Whoever stepped in first after the stirring of the water was healed of whatever disease he had. <u>5</u>A certain man was there, who had been sick for thirty-eight years. <u>6</u>When Jesus saw him lying there, and knew that he had been sick for a long time, he asked him,</p><h1>JESUS</h1><h2>Do you want to be made well?</h2><p>   <u>7</u>The sick man answered him,</p><h1>A SICK MAN AT THE POOL</h1><h4>Sir, I have no one to put me into the pool when the water is stirred up, but while I’m coming, another steps down before me.</h4><p>   <u>8</u>Jesus said to him,</p><h1>JESUS</h1><h2>Arise, take up your mat, and walk.</h2><p>   <u>9</u>Immediately, the man was made well, and took up his mat and walked. <br />   Now it was the Sabbath on that day. <u>10</u>So the Jews said to him who was cured,</p><h1>JEWISH LEADERS</h1><h4>It is the Sabbath. It is not lawful for you to carry the mat.</h4><p>   <u>11</u>He answered them,</p><h1>A SICK MAN AT THE POOL</h1><h4>He who made me well, the same said to me, ‘Take up your mat, and walk.’</h4><p>   <u>12</u>Then they asked him,</p><h1>JEWISH LEADERS</h1><h4>Who is the man who said to you, ‘Take up your mat, and walk’?</h4><p>   <u>13</u>But he who was healed didn’t know who it was, for Jesus had withdrawn, a crowd being in the place. <br />   <u>14</u>Afterward Jesus found him in the temple, and said to him,</p><h1>JESUS</h1><h2>Behold, you are made well. Sin no more, so that nothing worse happens to you.</h2><p>   <u>15</u>The man went away, and told the Jews that it was Jesus who had made him well. <u>16</u>For this cause the Jews persecuted Jesus, and sought to kill him, because he did these things on the Sabbath. <u>17</u>But Jesus answered them,</p><h1>JESUS</h1><h2>My Father is still working, so I am working, too.</h2><p><u>18</u>For this cause therefore the Jews sought all the more to kill him, because he not only broke the Sabbath, but also called God his own Father, making himself equal with God. <u>19</u>Jesus therefore answered them,</p><h1>JESUS</h1><h2>Most certainly, I tell you, the Son can do nothing of himself, but what he sees the Father doing. For whatever things he does, these the Son also does likewise. <u>20</u>For the Father has affection for the Son, and shows him all things that he himself does. He will show him greater works than these, that you may marvel. <u>21</u>For as the Father raises the dead and gives them life, even so the Son also gives life to whom he desires. <u>22</u>For the Father judges no one, but he has given all judgment to the Son, <u>23</u>that all may honor the Son, even as they honor the Father. He who doesn’t honor the Son doesn’t honor the Father who sent him. <br />   <u>24</u>Most certainly I tell you, he who hears my word, and believes him who sent me, has eternal life, and doesn’t come into judgment, but has passed out of death into life. <u>25</u>Most certainly, I tell you, the hour comes, and now is, when the dead will hear the Son of God’s voice; and those who hear will live. <u>26</u>For as the Father has life in himself, even so he gave to the Son also to have life in himself. <u>27</u>He also gave him authority to execute judgment, because he is a son of man. <u>28</u>Don’t marvel at this, for the hour comes, in which all that are in the tombs will hear his voice, <u>29</u>and will come out; those who have done good, to the resurrection of life; and those who have done evil, to the resurrection of judgment. <u>30</u>I can of myself do nothing. As I hear, I judge, and my judgment is righteous; because I don’t seek my own will, but the will of my Father who sent me. <br />   <u>31</u>If I testify about myself, my witness is not valid. <u>32</u>It is another who testifies about me. I know that the testimony which he testifies about me is true. <u>33</u>You have sent to John, and he has testified to the truth. <u>34</u>But the testimony which I receive is not from man. However, I say these things that you may be saved. <u>35</u>He was the burning and shining lamp, and you were willing to rejoice for a while in his light. <u>36</u>But the testimony which I have is greater than that of John, for the works which the Father gave me to accomplish, the very works that I do, testify about me, that the Father has sent me. <u>37</u>The Father himself, who sent me, has testified about me. You have neither heard his voice at any time, nor seen his form. <u>38</u>You don’t have his word living in you; because you don’t believe him whom he sent. <br />   <u>39</u>You search the Scriptures, because you think that in them you have eternal life; and these are they which testify about me. <u>40</u>Yet you will not come to me, that you may have life. <u>41</u>I don’t receive glory from men. <u>42</u>But I know you, that you don’t have God’s love in yourselves. <u>43</u>I have come in my Father’s name, and you don’t receive me. If another comes in his own name, you will receive him. <u>44</u>How can you believe, who receive glory from one another, and you don’t seek the glory that comes from the only God? <br />   <u>45</u>Don’t think that I will accuse you to the Father. There is one who accuses you, even Moses, on whom you have set your hope. <u>46</u>For if you believed Moses, you would believe me; for he wrote about me. <u>47</u>But if you don’t believe his writings, how will you believe my words?</h2><h5>*John 4:52 1:00 P. M.</h5>","path":"/342","Question1":"With which character do you most identify in this portion of Scripture? Why?","Question2":"What strengths and/or weaknesses do you see in this character in this portion of Scripture?","Question3":"What do you think God was wanting accomplish through the life of your selected character?","Question4":"What do you think God is wanting to accomplish through your life? How can you cooperate with his kind purposes in your life?","prevPath":"/341","nextPath":"/343"}},"pathContext":{}}

